.App {
  margin: 20px;
}


.content {
  display: flex;
  flex-direction: row;
}

.column {
  min-height: 300px;
  border-radius: 30px;
  box-shadow: 0 0 3px rgb(133, 133, 133);
  margin: 10px;
}

.app-form {
  width: 30%;
}

.main-content {
  width: 70%;
  padding: 40px;
}

.app-form{
  padding: 20px 40px;

}



.animal_temp {
  margin: 15px 0;
  padding: 10px;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.177);
}

.animal_temp:hover {
  box-shadow:  0 0 10px rgb(95, 95, 95);
}


.addNewAnimal-btn {
  background-color: rgb(255, 255, 255);
  padding: 10px 15px;
  border: 1px solid;
  outline: none;
  color: black;
  cursor: pointer;
}

.newAnimalName-input {
  padding: 10px 15px;
  border: none;
  border-bottom: 1px solid rgb(211, 211, 211);
  outline: none;
}

.newAnimalName-input:focus {
  background-color: rgb(211, 211, 211);
}


.createNewCell {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dotted rgb(178, 178, 178);
  color: rgb(205, 205, 205);
  user-select: none;
  cursor: pointer;
}

.createNewCell:hover {
  background-color: rgba(0, 0, 0, 0.045);
}

.cell {
  display: block;
  width: 150px;
  min-height: 150px;
  border: 1px solid black;
  cursor: pointer;
  user-select: none;
  margin: 10px;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 50px;
  border-radius: 5px;
}

.cells-items {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.animals-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  border-radius: 5px;
}

.cell-inner__animal {
  background-color: rgb(26, 26, 26);
  color: white;
  padding: 5px;
  margin: 4px;
  border-radius: 5px;
}

.cell-inner__animal:hover {
  box-shadow: 0px 0px 10px black;
}


.errorname {
  color: red;

}


.addNewAnimal-btn:disabled {
  color: rgb(192, 192, 192);
  border-color: beige;
  cursor: not-allowed;
}


.modal {
  padding: 20px;
  box-shadow: 0px 0px 5px rgb(92, 92, 92);
  z-index: 9;
}